.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;

}
.logo img{
    height: 4rem;
}

.navbar{
    background-color: #2b9ddf;
    width: 100%;
    color: #f4fbff;
    font-weight: 600;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 2.7em;
    padding: 0 1em ;
    font-size: 0.8em;
    transition: all 0.25s ease-in-out;
    z-index: 100;
}

.navbar ul {
    display: flex;
    flex-direction: row;
    max-width: 100%;
    height: 100%;
}
.navbar ul li {
    padding: 0 1em;
    height: 100%;
    /* display: flex;
    align-items: center;
    justify-content: center; */
    transition: all 1s ease-out;

}
.navbar ul li a{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

}


.navbar ul li:hover{
    background-color: #015c92;
    color: #f4fbff;
    
}

.navbar .account, .account > a{
    margin-right: 2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.navbar .account a{
    padding: 0.2rem 0.4rem;
    box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.2);
    background-color: #185adb;
    border-radius: 3px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
 a>p, a>i{
    display: block;
    height: min-content;
    padding: 0 3px;
}




